.carousel {
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px;
    background: rgba(0, 0, 0, 0.8); 
}

.carousel-inner {
    display: flex;
    transition: transform 0.3s ease-in-out;
}

.carousel-inner > div {
    flex: 0 0 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.carousel-button {
    position: absolute;
    z-index: 2;
    top: 50%;
    transform: translateY(-50%);
    background-color: #ffe100; 
    color: rgb(255, 255, 255);
    cursor: pointer;
    padding: 0px 20px;
    border-radius: 50%;
    font-size: 3rem;
    border: none;
    box-shadow: 0 10px #b39f07;
}

.carousel-button.prev {
    left: 30px;
}

.carousel-button.next {
    right: 30px;
}

.card {
    margin: 10px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    background-size: cover; 
    background-position: center; 
    width: 100%; 
    height: 300px; 
}

