/* ------------------- APP --------------------- */
.app {
  text-align: center;
}

.app-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .app-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.app-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.app-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-input {
  width: 100%;
  height: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 20px;
  margin-bottom: 20px;
  background-color: transparent;
  color: #81e6d3;
  text-transform: uppercase;
  text-align: center;
}

.app-input:focus {
  outline: none;
  border-color: #81e6d3;
}
.app-input::placeholder {
  color: #99b3ae;
}
.app-input:hover {
  border-color: #ffd826;
}

.app-button {
  background-color: #fa519a; 
  color: #ffeef5;
  padding: 40px 25px;
  border: none;
  border-radius: 100%; 
  cursor: pointer;
  font-size: 2rem;
  font-weight: 900;
  text-transform: uppercase;
  transition: background-color 0.3s, box-shadow 0.2s, transform 0.2s;
  box-shadow: 0 10px #e91571;
}

.app-button:hover {
  background-color: #f74d90; 
  transform: scale(1.1);
}

.app-button:active {
  box-shadow: 0 1px #f74d90; 
  transform: translateY(3px); 
}

/* --------------------- HOMEPAGE --------------------- */
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.container-hp {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('./assets/images/background.png');
  background-size: cover;
  background-position: center;
  backdrop-filter: blur(100px);
}
.header-hp{
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #282c34;
  border-bottom: 2px solid #ffd826;
}
.logo {
  margin-top: 80px;
  height: 30vmin;
}

.logo:hover {
  animation: App-logo-spin infinite 20s linear;
}

.title-hp {
  font-size: 3rem;
  text-transform: uppercase;
  text-align: center;
  margin-right: 200px;
  color: #fff;
  text-shadow:
  0 0 3px rgba(0, 255, 255, 0.7),
  0 0 6px rgba(0, 255, 255, 0.7),
  0 0 9px rgba(0, 255, 255, 0.7),
  0 0 12px rgba(0, 255, 255, 0.7);
}

.container-carousel {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.subtitle-hp {
  font-size: 48px;
  color: #F0F; 
  text-shadow: 
    1px 1px 0 #EB0,   
    2px 2px 0 #0D6,   
    3px 3px 0 #09F; 
  font-family: 'Press Start 2P', cursive; 
}
.jouer-button {
  background-color: #09F;
  text-transform: uppercase;
  border: none;
  color: white;
  padding: 30px 10px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 2rem;
  box-shadow: 0 10px rgb(1, 85, 141);
}

.jouer-button:hover {
  background-color: #09F;
  transition: transform 0.1s ease;
}

.bouton-jouer:active {
  transform: scale(0.95); 
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}

.relief-text {
  font-size: 4rem;
  color: #e55800;
  text-shadow: 
      1px 1px 0 #000,   
      2px 2px 0 #000,   
      3px 3px 0 #000;
}